@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Regular.woff2");
  font-weight: 400;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Medium.woff2");
  font-weight: 500;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-SemiBold.woff2");
  font-weight: 600;
}
@font-face {
  font-family: "NeueMachina";
  src: url("../fonts/NeueMachina/NeueMachina-Regular.woff2");
  font-weight: 400;
}
@font-face {
  font-family: "DrukWide";
  src: url("../fonts/DrukWide/DrukWide-Medium.woff2");
  font-weight: 500;
}
@font-face {
  font-family: "FontAwesome";
  src: url("../fonts/FontAwesome/FontAwesome-400.woff2");
  font-weight: 400;
}
@font-face {
  font-family: "FontAwesome";
  src: url("../fonts/FontAwesome/FontAwesome-900.woff2");
  font-weight: 900;
}
@font-face {
  font-family: "FontAwesome";
  src: url("../fonts/FontAwesome/FontAwesome-Brands.woff2");
  font-weight: 500;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.wrapper {
  background: #111111;
  font-family: "Montserrat";
  background-image: url("../images/glass.png");
}
.wrapper *::before,
.wrapper *::after {
  font-family: "FontAwesome";
}

.icon-plus::before {
  content: "+";
  color: #FFFFFF;
  font-weight: 900;
}
.icon-search::before {
  content: "\f002";
  color: #FFFFFF;
  font-weight: 900;
}
.icon-telegram::before {
  content: "\f2c6";
  color: #FFFFFF;
  font-weight: 500;
}
.icon-instagram::before {
  content: "\f16d";
  color: #FFFFFF;
  font-weight: 500;
}
.icon-youtube::before {
  content: "\f167";
  color: #FFFFFF;
  font-weight: 500;
  background: transparent;
}
.icon-arrow_right::before {
  content: "\f178";
  color: #01DE08;
  font-weight: 900;
}
.icon-arrow_left::before {
  content: "\f177";
  color: #01DE08;
  font-weight: 900;
}

.promo__descr-buttons button {
  color: #FFFFFF;
  font-size: 20px;
  font-family: "Montserrat";
  font-weight: 500;
  border: 1px solid;
  border-radius: 100px;
  background: transparent;
  padding: 24px 48px;
  transition: 0.2s;
  cursor: pointer;
}
.promo__descr-buttons button:hover {
  opacity: 0.75;
}

.footer__contacts-title, .footer__about-title, .footer__pages-title {
  color: #FFFFFF;
  font-family: "Montserrat";
  font-size: 32px;
  font-weight: 600;
}
.footer__contacts-link, .footer__about-text, .footer__pages-link {
  color: #7A7A7A;
  font-family: "Montserrat";
  font-size: 24px;
  font-weight: 400;
}

.products__item-price {
  color: #FFFFFF;
  font-family: "Montserrat";
  font-size: 32px;
  font-weight: 600;
}
.products__item-title {
  color: #FFFFFF;
  font-family: "Montserrat";
  font-size: 24px;
  font-weight: 400;
}

.header {
  max-width: 1370px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 1370px) {
  .header {
    max-width: 1190px;
  }
}
@media (max-width: 1190px) {
  .header {
    max-width: 992px;
  }
}
@media (max-width: 992px) {
  .header {
    max-width: 768px;
  }
}
@media (max-width: 768px) {
  .header {
    max-width: 576px;
  }
}
@media (max-width: 576px) {
  .header {
    max-width: 100%;
  }
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 64px 0;
}
.header__nav-logo {
  width: 238px;
  height: 38px;
}
.header__nav-logo img {
  width: 100%;
  height: 100%;
}
.header__nav-list {
  display: flex;
  align-items: center;
}
.header__nav-item:not(:last-child) {
  margin-right: 70px;
}
.header__nav-link {
  color: #FFFFFF;
  font-size: 20px;
  transition: 0.2s;
}
.header__nav-link:hover {
  color: #c1c1c1;
}

.promo {
  max-width: 1370px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}
@media (max-width: 1370px) {
  .promo {
    max-width: 1190px;
  }
}
@media (max-width: 1190px) {
  .promo {
    max-width: 992px;
  }
}
@media (max-width: 992px) {
  .promo {
    max-width: 768px;
  }
}
@media (max-width: 768px) {
  .promo {
    max-width: 576px;
  }
}
@media (max-width: 576px) {
  .promo {
    max-width: 100%;
  }
}
.promo__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.promo__title {
  color: #FFFFFF;
  font-family: "DrukWide";
  font-size: 86px;
  font-weight: 500;
  width: 580px;
}
.promo__title span {
  background: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
}
.promo__title span img {
  width: 70px;
  height: 70px;
  position: absolute;
  right: -24px;
  bottom: -16px;
}
.promo__descr-text {
  color: #FFFFFF;
  font-size: 32px;
  font-weight: 600;
  margin-bottom: 32px;
  width: 536px;
}
.promo__descr-text img {
  margin-left: 16px;
}
.promo__descr-buttons {
  display: flex;
  align-items: center;
}
.promo__descr-buttons button:first-child {
  background: #01DE08;
  color: #000000;
  margin-right: 45px;
  border-color: #01DE08;
}
.promo__descr-buttons button:last-child {
  border-color: #00FDD9;
}
.promo__models {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 64px 0 96px;
}
.promo__item {
  width: 400px;
  height: 100%;
  position: relative;
  z-index: 1;
}
.promo__item-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 332px;
  overflow: hidden;
}
.promo__item-overlay img {
  width: 100%;
  height: 100%;
  transition: 0.2s;
  margin-bottom: -25px;
}
.promo__item-add {
  position: absolute;
  left: 50%;
  bottom: 0%;
  transform: translate(-50%, 50%);
  width: 64px;
  height: 64px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(8px);
  z-index: 1;
  cursor: pointer;
  transition: 0.2s;
}
.promo__item::before {
  content: "";
  width: 100%;
  min-height: 156px;
  background: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
  border-radius: 100px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.promo__item::after {
  content: "";
  width: 100%;
  min-height: 156px;
  background: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
  border-radius: 100px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  filter: blur(20px);
  opacity: 0;
  transition: 0.2s linear;
}
.promo__item:hover::after {
  opacity: 1;
}
.promo__item:hover .promo__item-overlay img {
  margin-bottom: 0;
}
.promo__item:hover .promo__item-add {
  transform: translate(-50%, 50%) scale(1.05);
}
.promo__backlights {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
.promo__backlights-blur {
  width: min(823px, 45%);
  height: min(380px, 20rem);
  background: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
  border-radius: 823px;
  filter: blur(60px);
  position: absolute;
  opacity: 0.25;
}
.promo__backlights-blur:first-child {
  right: 3%;
  top: 3%;
}
.promo__backlights-blur:nth-child(2) {
  left: -2%;
  bottom: 10%;
}
.promo__backlights-blur:last-child {
  right: -2%;
  bottom: -3%;
}

.products {
  max-width: 1370px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  padding-block: 96px;
}
@media (max-width: 1370px) {
  .products {
    max-width: 1190px;
  }
}
@media (max-width: 1190px) {
  .products {
    max-width: 992px;
  }
}
@media (max-width: 992px) {
  .products {
    max-width: 768px;
  }
}
@media (max-width: 768px) {
  .products {
    max-width: 576px;
  }
}
@media (max-width: 576px) {
  .products {
    max-width: 100%;
  }
}
.products__category {
  display: flex;
  align-items: center;
  margin-bottom: 96px;
}
.products__category label {
  color: #7A7A7A;
  font-family: Montserrat;
  font-size: 32px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s;
  position: relative;
  padding: 0 4px 12px;
  display: flex;
  justify-content: center;
}
.products__category label:not(:last-child) {
  margin-right: 48px;
}
.products__category label::before {
  content: "";
  height: 7px;
  width: 0;
  background: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
  position: absolute;
  bottom: 0;
  border-radius: 60px;
  transition: 0.2s ease-out;
}
.products__category input {
  display: none;
  width: 0;
  height: 0;
  position: absolute;
  margin-left: -200vw;
  visibility: hidden;
  appearance: none;
}
.products__category input:checked + label {
  color: #FFFFFF;
}
.products__category input:checked + label::before {
  width: 100%;
}
.products__category input:not(:checked) + label:hover {
  opacity: 0.8;
}
.products__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 90px;
}
.products__item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border-radius: 48px;
  overflow: hidden;
  border: 2px solid #7A7A7A;
  transition: 0.2s linear;
}
.products__item:hover {
  box-shadow: 1px 0px 20px 5px rgba(255, 255, 255, 0.2);
}
.products__item:hover .products__item-pic {
  transform: scale(1.05);
}
.products__item-img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: #FEFEFE;
  position: relative;
}
.products__item-img .products__item-pic {
  width: 340px;
  max-height: 305px;
  height: auto;
  transition: 0.2s;
}
.products__item-preview {
  position: absolute;
  left: 24px;
  top: 24px;
  display: flex;
  flex-direction: column;
}
.products__item-preview input {
  display: none;
  width: 0;
  height: 0;
  position: absolute;
  margin-left: -200vw;
  visibility: hidden;
  appearance: none;
}
.products__item-preview label {
  padding: 4px;
  border-radius: 8px;
  transition: 0.2s linear;
  cursor: pointer;
}
.products__item-preview label:first-of-type {
  margin-bottom: 8px;
}
.products__item-preview label img {
  width: 46px;
  height: 46px;
}
.products__item-preview label:hover {
  box-shadow: 0px 0px 4px rgba(38, 38, 38, 0.5019607843);
}
.products__item-preview input:checked + label {
  box-shadow: 0px 0px 4px rgba(38, 38, 38, 0.5019607843);
}
.products__item-preview input:not(:checked) + label {
  opacity: 0.8;
}
.products__item-descr {
  width: 100%;
  height: 100%;
  padding: 24px 24px 96px;
}
.products__item-price {
  margin-bottom: 24px;
}
.footer {
  padding: 96px 0 128px;
  background: #2E2E2E;
  border-radius: 64px 64px 0 0px;
}
.footer__content {
  max-width: 1370px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
@media (max-width: 1370px) {
  .footer__content {
    max-width: 1190px;
  }
}
@media (max-width: 1190px) {
  .footer__content {
    max-width: 992px;
  }
}
@media (max-width: 992px) {
  .footer__content {
    max-width: 768px;
  }
}
@media (max-width: 768px) {
  .footer__content {
    max-width: 576px;
  }
}
@media (max-width: 576px) {
  .footer__content {
    max-width: 100%;
  }
}
.footer__pages-title {
  margin-bottom: 64px;
}
.footer__pages-list {
  list-style: disc;
  color: #7A7A7A;
}
.footer__pages-item:not(:last-child) {
  margin-bottom: 26px;
}
.footer__pages-link {
  transition: 0.2s;
}
.footer__pages-link:hover {
  color: #ababab;
}
.footer__about {
  width: 462px;
}
.footer__about-title {
  margin-bottom: 64px;
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.footer__contacts-title {
  margin-bottom: 38px;
}
.footer__contacts-link {
  margin-bottom: 38px;
  transition: 0.2s;
}
.footer__contacts-link:hover {
  color: #ababab;
}
.footer__contacts-socials {
  display: flex;
  align-items: center;
}
.footer__contacts-socials a.footer__contacts-icon {
  font-size: 40px;
  transition: 0.2s;
  padding: 2px;
}
.footer__contacts-socials a.footer__contacts-icon:not(:last-child) {
  margin-right: 32px;
}
.footer__contacts-socials a.footer__contacts-icon:hover {
  transform: scale(0.9);
  opacity: 0.7;
}