$fonts: ('Montserrat', 'Regular', 400),
        ('Montserrat', 'Medium', 500),
        ('Montserrat', 'SemiBold', 600),
        ('NeueMachina', 'Regular', 400),
        ('DrukWide', 'Medium', 500),
        ('FontAwesome', 400, 400),
        ('FontAwesome', 900, 900),
        ('FontAwesome', 'Brands', 500);

@each $name, $nameW, $w in $fonts {
    @font-face {
        font-family: $name;
        src: url('../fonts/#{$name}/#{$name}-#{$nameW}.woff2');
        font-weight: $w;
    }
}

$backlight: linear-gradient(90deg, #01DE08 0%, #00FFE0 100%);
$main: #111111;
$text: #FFFFFF;
$text_hover: #c1c1c1;
$extra: #7A7A7A;
$extra_hover: #ababab;
$green: #01DE08;
$btn-text: #000000;
$add-border: #FFFFFF33;
$extra-bg: #2E2E2E;
$blue: #00FDD9;
$product-bg: #FEFEFE;
$preview-shadow: #26262680;