.header {
    @include container;
    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 64px 0;

        &-logo {
            width: 238px;
            height: 38px;

            img {
                width: 100%;
                height: 100%;
            }
        }

        &-list {
            display: flex;
            align-items: center;
        }

        &-item:not(:last-child) {
            margin-right: 70px;
        }

        &-link {
            color: $text;
            font-size: 20px;
            transition: .2s;

            &:hover {
                color: $text_hover;
            }
        }
    }
}