* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

.wrapper {
    background: $main;
    font-family: "Montserrat";
    background-image: url("../images/glass.png");
    *::before,
    *::after {
        font-family: "FontAwesome";
    }
}

.icon {
    &-plus::before {
        content: "\2b";
        color: $text;
        font-weight: 900;
    }

    &-search::before {
        content: "\f002";
        color: $text;
        font-weight: 900;
    }

    &-telegram::before {
        content: "\f2c6";
        color: $text;
        font-weight: 500;
    }

    &-instagram::before {
        content: "\f16d";
        color: $text;
        font-weight: 500;
    }

    &-youtube::before {
        content: "\f167";
        color: $text;
        font-weight: 500;
        background: transparent;
    }

    &-arrow_right::before {
        content: "\f178";
        color: $green;
        font-weight: 900;
    }

    &-arrow_left::before {
        content: "\f177";
        color: $green;
        font-weight: 900;
    }
}

%button {
    color: $text;
    font-size: 20px;
    font-family: "Montserrat";
    font-weight: 500;
    border: 1px solid;
    border-radius: 100px;
    background: transparent;
    padding: 24px 48px;
    transition: 0.2s;
    cursor: pointer;

    &:hover {
        opacity: 0.75;
    }
}

%footer {
    &-title {
        color: $text;
        font-family: "Montserrat";
        font-size: 32px;
        font-weight: 600;
    }

    &-text {
        color: $extra;
        font-family: "Montserrat";
        font-size: 24px;
        font-weight: 400;
    }
}

%products {
    &-price {
        color: $text;
        font-family: "Montserrat";
        font-size: 32px;
        font-weight: 600;
    }

    &-title {
        color: $text;
        font-family: "Montserrat";
        font-size: 24px;
        font-weight: 400;
    }
}
