.products {
    @include container;
    padding-block: 96px;

    &__category {
        display: flex;
        align-items: center;
        margin-bottom: 96px;

        label {
            color: $extra;
            font-family: Montserrat;
            font-size: 32px;
            font-weight: 600;
            cursor: pointer;
            transition: 0.2s;
            position: relative;
            padding: 0 4px 12px;
            display: flex;
            justify-content: center;

            &:not(:last-child) {
                margin-right: 48px;
            }

            &::before {
                content: "";
                height: 7px;
                width: 0;
                background: $backlight;
                position: absolute;
                bottom: 0;
                border-radius: 60px;
                transition: 0.2s ease-out;
            }
        }

        input {
            display: none;
            width: 0;
            height: 0;
            position: absolute;
            margin-left: -200vw;
            visibility: hidden;
            appearance: none;
        }

        input:checked + label {
            color: $text;

            &::before {
                width: 100%;
            }
        }

        input:not(:checked) + label:hover {
            opacity: 0.8;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 90px;
    }

    &__item {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border-radius: 48px;
        overflow: hidden;
        border: 2px solid $extra;
        transition: 0.2s linear;

        &:hover {
            box-shadow: 1px 0px 20px 5px $add_border;

            .products__item-pic {
                transform: scale(1.05); 
            }
        }

        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background: $product-bg;
            position: relative;

            .products__item-pic {
                width: 340px;
                max-height: 305px;
                height: auto;
                transition: 0.2s;
            }
        }

        &-preview {
            position: absolute;
            left: 24px;
            top: 24px;
            display: flex;
            flex-direction: column;

            input {
                display: none;
                width: 0;
                height: 0;
                position: absolute;
                margin-left: -200vw;
                visibility: hidden;
                appearance: none;
            }

            label {
                padding: 4px;
                border-radius: 8px;
                transition: .2s linear;
                cursor: pointer;

                &:first-of-type {
                    margin-bottom: 8px;
                }

                img {
                    width: 46px;
                    height: 46px;
                }

                &:hover {
                    box-shadow: 0px 0px 4px $preview-shadow;
                }
            }

            input:checked + label {
                box-shadow: 0px 0px 4px $preview-shadow;
            }

            input:not(:checked) + label {
                opacity: .8;
            }
        }

        &-descr {
            width: 100%;
            height: 100%;
            padding: 24px 24px 96px;
        }

        &-price {
            @extend %products-price;
            margin-bottom: 24px;
        }

        &-title {
            @extend %products-title;
        }
    }
}
