.promo {
    @include container;
    position: relative;
    z-index: 1;


    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__title {
        color: $text;
        font-family: "DrukWide";
        font-size: 86px;
        font-weight: 500;
        width: 580px;

        span {
            background: $backlight;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            position: relative;

            img {
                width: 70px;
                height: 70px;
                position: absolute;
                right: -24px;
                bottom: -16px;
            }
        }
    }

    &__descr {
        &-text {
            color: $text;
            font-size: 32px;
            font-weight: 600;
            margin-bottom: 32px;
            width: 536px;

            img {
                margin-left: 16px;
            }
        }

        &-buttons {
            display: flex;
            align-items: center;

            button:first-child {
                background: $green;
                color: $btn-text;
                margin-right: 45px;
                border-color: $green;
            }

            button:last-child {
                border-color: $blue;
            }

            button {
                @extend %button;
            }
        }
    }

    &__models {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 64px 0 96px;
    }

    &__item {
        width: 400px;
        height: 100%;
        position: relative;
        z-index: 1;

        &-overlay {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 332px;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                transition: 0.2s;
                margin-bottom: -25px;
            }
        }

        &-add {
            position: absolute;
            left: 50%;
            bottom: 0%;
            transform: translate(-50%, 50%);

            width: 64px;
            height: 64px;
            font-size: 16px;

            display: flex;
            align-items: center;
            justify-content: center;

            border-radius: 50%;
            border: 1px solid $add-border;

            background: $add-border;
            backdrop-filter: blur(8px);

            z-index: 1;

            cursor: pointer;
            transition: .2s;
        }

        &::before {
            content: "";
            width: 100%;
            min-height: 156px;
            background: $backlight;
            border-radius: 100px;
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: -1;
        }

        &::after {
            content: "";
            width: 100%;
            min-height: 156px;
            background: $backlight;
            border-radius: 100px;
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: -1;
            filter: blur(20px);
            opacity: 0;
            transition: 0.2s linear;
        }

        &:hover::after {
            opacity: 1;
        }

        &:hover &-overlay img {
            margin-bottom: 0;
        }

        &:hover &-add {
            transform: translate(-50%, 50%) scale(1.05);
        }
    }

    &__backlights {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        
        &-blur {
            width: min(823px, 45%);
            height: min(380px, 20rem);
            background: $backlight;
            border-radius: 823px;
            filter: blur(60px);
            position: absolute;
            opacity: .25;
        }

        &-blur:first-child {
            right: 3%;
            top: 3%;
        }

        &-blur:nth-child(2) {
            left: -2%;
            bottom: 10%;
        }
        
        &-blur:last-child {
            right: -2%;
            bottom: -3%;
        }
    }
}
