$xl: 1370px;
$lg: 1190px;
$md: 992px;
$sm: 768px;
$xs: 576px;
$gutters: 30px;
$gutters-side: calc($gutters / 2);

@mixin container($padding: $gutters-side) {
    max-width: $xl;
    width: 100%;
    padding: 0 $padding;
    margin: 0 auto;

    @include media($xl) {
        max-width: $lg;
    }
    @include media($lg) {
        max-width: $md;
    }    
    @include media($md) {
        max-width: $sm;
    }    
    @include media($sm) {
        max-width: $xs;
    }    
    @include media($xs) {
        max-width: 100%;
    }
}

@mixin media($size: $xl) {
    @media (max-width: $size) {
        @content;
    }
}