.footer {
    padding: 96px 0 128px;
    background: $extra-bg;
    border-radius: 64px 64px 0 0px;

    &__content {
        @include container;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    &__pages {
        &-title {
            @extend %footer-title;
            margin-bottom: 64px;
        }

        &-list {
            list-style: disc;
            color: $extra;
        }

        &-item:not(:last-child) {
            margin-bottom: 26px;
        }

        &-link {
            @extend %footer-text;
            transition: .2s;

            &:hover {
                color: $extra_hover;
            }
        }

    }

    &__about {
        width: 462px;
        &-title {
            @extend %footer-title;
            margin-bottom: 64px;
        }

        &-text {
            @extend %footer-text;
        }
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        &-title {
            @extend %footer-title;
            margin-bottom: 38px;
        }

        &-link {
            @extend %footer-text;
            margin-bottom: 38px;
            transition: .2s;

            &:hover {
                color: $extra_hover;
            }
        }

        &-socials {
            display: flex;
            align-items: center;

            a.footer__contacts-icon {
                font-size: 40px;
                transition: .2s;
                padding: 2px;

                &:not(:last-child) {
                    margin-right: 32px;
                }

                &:hover {
                    transform: scale(.9);
                    opacity: .7;
                }
            }
        }
    }
    
}
